:host {
  display: inline-grid;
  overflow: hidden;
  width: 100%;
}

.view-email {
  width: 100%;

  div.gmail_quote {
    display: none;
    color: var(--nylas-color-primary-400);

    &.show {
      display: block;
    }
  }

  blockquote {
    border-color: var(--nylas-color-primary-500) !important;
  }

  .nylas-view-email-toggle-button {
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    background-color: var(--nylas-color-primary-200);
    color: var(--nylas-color-primary-600);
    border: 0;
    border-radius: 2px;
    font-size: 14px;
    font-weight: 500;
    line-height: 1.5;
    transition: all 0.2s ease-in-out;

    &:hover {
      background-color: var(--nylas-color-primary-200);
      color: var(--nylas-color-primary-600);
    }
  }
}

